<template>
  <div class="user-box">
    <HeadTop :title="$t('index.加入合伙人')" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <div class="nft-box">
      <div class="title"> {{$t('index.加入合伙人获取元素世界勋章NFT')}}</div>
      <div class="desc">
         {{$t('index.直接邀请至少个用户', {introNum: introNum, buyNftAmount: buyNftAmount})}}
      </div>
      <div class="btn-box" v-if="Number(endTime) > 0">
         <van-button :disabled="isBuyNft" :loading="btn.loading" v-if="Number(usdtAllowance) > 0" type="primary" size="large" @click="buyNft"> {{$t('index.加入合伙人')}}</van-button>
          <van-button v-else type="primary" :loading="btn.loading" size="large" @click="approveUsdt"> {{$t('index.授权')}} USDT</van-button>
      </div> 
      <div class="btn-box" v-else>
         <van-button type="primary" size="large" >已结束</van-button>
      </div>
      <div class="children-num">{{$t('index.ido直推有效人数：')}}{{childrenNum}}</div>
      <div class="tip"> {{$t('index.花费USDT获得NFT', {buyNftAmount, introNum})}}</div>
    </div>
    <div class="intro-box">
      <div class="title">{{$t('index.我的IDO直推')}}</div>
      <div class="list">
        <div class="item flex-middle" v-for="(item, index) in children" :key="index">
         <div class="token flex-item">{{item.substring(0, 12)}}****{{item.substring(item.length - 12, item.length)}}</div>
         <div class="copy-btn" :data-clipboard-text="item">{{$t('index.复制')}}</div>
        </div>
      </div>
    </div>
    <!-- <div class="nft-box">
      <div class="title">IDO</div>
       <div class="desc">
         {{$t('index.花费USDT获得$ELE', {idoAmount: idoAmount, num: idoAmount})}}
      </div>
      <div class="btn-box">
         <van-button :disabled="isIdo" v-if="Number(usdtAllowance) > 0" type="primary" size="large" @click="ido">IDO</van-button>
         <van-button v-else type="primary" size="large" @click="approveUsdt"> {{$t('index.授权')}} USDT</van-button>
      </div>
    </div> -->
  </div>  
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useStore } from "vuex";
import config from "@/utils/config";
import idoAbi from "@/utils/idoAbi";
import BigNumber from 'bignumber.js';
import tokenAbi from "@/utils/abi";
import { intro } from '@/storage'
import { useI18n } from "vue-i18n"
import { MaxUint256 } from "@ethersproject/constants";
import { Toast } from 'vant';
import Clipboard from 'clipboard'

export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      idoContract: null,
      usdtContract: null,
      usdtBalance: '0.00',
      idoAmount: '0.00',
      buyNftAmount: '0.00',
      usdtAllowance: '',
      introNum: 0,
      intro: intro.get() || config.rootAddress,
      isBuyNft: false,
      isGetNft: false,
      isIdo: false,
      childrenNum: 0,
      children:[],
      btn: {
        loading: false
      },
      endTime: 0,
      initContract: () => {
        data.idoContract = new data.web3.eth.Contract(idoAbi, config.idoAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.initData()
      },
      initData: () => {
        data.getBalance()
        data.getIdoInfo()
        data.getUsdtAllowance()
        data.getUserInfo()
        data.getEndTime()
        setInterval(() => {
          data.getEndTime()
        }, 3000)
      },
      getEndTime: async () => {
        let time = await data.idoContract.methods.getEndTime().call()
        data.endTime = time
      },
      getUserInfo: async () => {
        let userInfo = await data.idoContract.methods.userInfo(data.address).call()
        let children = await data.idoContract.methods.getChildren(data.address).call()
        data.isBuyNft = userInfo.isGetNft
        data.isGetNft = userInfo.isGetNft
        data.isIdo = userInfo.isIdo
        data.childrenNum = children.length
        data.children = children
      },
       getBalance: async () => {
        let usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call()
        data.usdtBalance =  new BigNumber(usdtBalance).dividedBy(10 ** 18).toFixed(2,1)
      },
      getIdoInfo: async () => {
        let idoAmount = await data.idoContract.methods.idoAmount().call()
        let buyNftAmount = await data.idoContract.methods.buyNftAmount().call()
        let introNum = await data.idoContract.methods.introNum().call()
        data.idoAmount =  new BigNumber(idoAmount).dividedBy(10 ** 18).toFixed(2,1)
        data.buyNftAmount =  new BigNumber(buyNftAmount).dividedBy(10 ** 18).toFixed(2,1)
        data.introNum = introNum
      },
      getUsdtAllowance: async () => {
        let result = await data.usdtContract.methods.allowance(data.address, config.idoAddress).call();
        data.usdtAllowance = result;
      },
      approveUsdt: async () => {
        data.btn.loading = true
        try {
          await data.usdtContract.methods.approve(config.idoAddress, MaxUint256).send({ from: data.address });
          data.getUsdtAllowance()
           data.btn.loading = false
        } catch (error) {
           data.btn.loading = false
          console.log(error)
        }
      },
      buyNft: async () => {
        let userInfo = await data.idoContract.methods.userInfo(data.address).call()
       
        if(userInfo.isBuyNft) {
           Toast('此地址已经购买过了')
          return
        }
        if (Number(data.usdtBalance) < Number(data.buyNftAmount)) {
          Toast(t('index.USDT余额不足'))
          return
        }
         data.btn.loading = true
        try {
          await data.idoContract.methods.buyNft(data.intro).send({ from: data.address });
          Toast(t('index.操作成功'))
           data.btn.loading = false
          data.initData()
        } catch (error) {
           data.btn.loading = false
          console.log(error)
        }
      },
       ido: async () => {
        if (Number(data.usdtBalance) < Number(data.buyNftAmount)) {
          Toast(t('index.USDT余额不足'))
          return
        }
         data.btn.loading = true
        try {
          await data.idoContract.methods.ido(data.intro).send({ from: data.address });
          Toast(t('index.操作成功'))
           data.btn.loading = false
          data.initData()
        } catch (error) {
          data.btn.loading = false
          console.log(error)
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    onMounted(() => {
      var clipboard = new Clipboard('.copy-btn')
      clipboard.on('success', function (e) {
        Toast(t('index.复制成功'))
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast(t('index.复制失败，请手动复制'))
      })
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.user-box{
  padding: 20px 15px 0;
}
.children-num{
  font-size: 14px;
  color: #a3ed3e;
   margin-top: 15px;
}
.nft-box{
  padding: 20px;
  margin-bottom: 15px;
  background: linear-gradient(0deg, rgba(34, 37, 46, 0.64) 1%, rgba(34, 37, 46, 0.72) 100%);
  border: 1px solid rgba(101, 255, 0, 0.4);
  border-radius: 10px;
  .title{
    font-size: 18px;
    color: #a3ed3e;
    margin-bottom: 10px;
  }
  .desc{
    font-size: 15px;
    color: #fff;
    margin-bottom: 20px;
    word-break: break-all;
  }
  .tip{
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px;
    margin-top: 10px;
  }
}
.intro-box{
  padding: 15px 0;
  color: #fff;
  .title{
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  .item{
  height: 40px;
  line-height: 40px;
  padding: 0 15px;
  color: #fff;
  background: rgba(255,255,255,0.15);
  .copy-btn{
    background: #3cd4a2;
    height: 26px;
    font-size: 14px;
    line-height: 26px;
    padding: 0 10px;
    border-radius: 13px;
  }
}
}
</style>
